const AppContainer = styled.div`
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
`;

function App () {
  return (
<Router>
  <Switch>
    <Route exact path="/">
      {/* Login Form */}
      <AppContainer> 
        <AccountBox />
      </AppContainer>
    </Route>

    <Route>
      <Home />
    </Route>
  </Switch>
 </Router>
  );
}

export default App;